html{
    background-color: #fafafa;
    font-family: 'Roboto', Helvetica, Arial, sans-serif;
    font-weight: 300;
    font-size: 1em;
    height: 100%;
    margin: 0px;
    padding: 0px;
}
body{
  height: 100%;
  margin: 0px;
  padding: 0px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
a{
	text-decoration: none;
	color: #ff6600;
	font-weight: 400;
	cursor: pointer;
  padding: 3px 3px 3px 3px;
  margin-right: -6px;
  position: relative;
  left: -3px;
}
a.nodecoration:hover{
	text-decoration: none;
}
a:hover:not(.nohover), a.active{
  background: #ff6600;
  color: white;
  border-radius: 0px;
}
a img:hover {
  background: none !important;
}
a.button, .button a{
  border: 2px solid #ff6600;
  display: inline-block;
  margin: 2px;
  background: #ff6600;
  color: white;
}
a.button:hover, .button a:hover{
  background: white;
  color: #ff6600;
}
a[disabled] {
	text-decoration: none;
	color: gray;
	pointer-events: none;
	cursor: not-allowed !important;
}
